import React from 'react'
import { Link } from "react-router-dom";
import './css.css'

export default function NoPage(props) {

    return (
        <div>
        <p className="nouser">Esta pgina no est disponible.</p>
          {props.construction 
          ?<p className="nouser-desc">Esta pgina an se encuentra en construccin. <Link to="/">Volver a Pupigram.</Link>
          </p>
          :<p className="nouser-desc">Es posible que el enlace que has seguido sea incorrecto o que se haya
          eliminado la pgina. <Link to="/">Volver a Pupigram.</Link>
          </p>}
      </div>
    )
}
